	module.exports = {"data":{"site":{"siteMetadata":{"title":"Voracious Bookworm","author":"Lei"}},"markdownRemark":{"id":"/home/lei-lo/Code/voraciouscoder.github.io/src/pages/reading-roundup-17/index.md absPath of file >>> MarkdownRemark","html":"<p>Happy belated Canada Day! Iâ€™m immensely grateful to be Canadian ðŸ‡¨ðŸ‡¦ Iâ€™ll have to compile a list of my fave Canadian authors someday. And get to those Louise Penny books Iâ€™ve been meaning to read.</p>\n<p><strong>A River Runs Through It</strong><br>\n<em>Goodreads Summer Reading Challenge: Hook â€˜Em</em></p>\n<p>I considered a few books for this challenge (like <em>Trout Fishing in America</em>) and got partway through <em>The History of Fly-Fishing in 50 Flies</em> but <em>A River Runs Through It</em> won out because it was at my parentsâ€™ house.</p>\n<p>Growing up in a city with a couple big rivers and an outdoorsy vibe gave me opportunities to watch people fly-fishing. Trying to read the history book showed me how long the sport has been around. But this book was perfect for getting at why so many people love it. The descriptions of fishing have stayed in my head more than the sad family story woven into the scenery.</p>\n<p>So to me, this is more of a sit back, relax, and enjoy the meditative descriptions book. Almost like fly-fishing itself.</p>","frontmatter":{"title":"Reading Roundup 17","date":"July 03, 2018"}}},"pathContext":{"slug":"/reading-roundup-17/","previous":{"fields":{"slug":"/reading-roundup-16/"},"frontmatter":{"title":"Reading Roundup 16"}},"next":{"fields":{"slug":"/reading-roundup-18/"},"frontmatter":{"title":"Reading Roundup 18"}}}}

